@import "~antd/dist/antd.css";
@import url("https://fonts.googleapis.com/css?family=Montserrat|Roboto&display=swap");

$fontSize1: 10.5px;
$fontSize2: 13px;
$fontFamily1: "Roboto";
$textColor1: white;
$textColor2: rgba(245, 245, 245, 0.5);
$textColor3: rgb(139, 139, 139);
$color1: white;

* {
  font-family: $fontFamily1;
}

.header {
  padding: 0;
  overflow: hidden;
}

.mainTitle {
  color: $textColor1;
  padding-left: 20px;
  width: 200px;
  float: left;
  span {
    color: $textColor2;
  }
}

.topMenuItem {
  font-size: $fontSize1;
}

.sider {
  background-color: $color1;
}

.leftMenuItem span {
  font-size: $fontSize1;
}

.item {
  overflow: hidden;
}

.formLabel {
  font-family: "Roboto";
  font-size: $fontSize2;
}

.dataRangeFields {
  color: $textColor3;
  font-size: 11px;
  flex: 1;
}

.drawerButton {
  width: 35px;
  height: 30px;
  position: absolute;
  top: 100px;
  background-color: white;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  &:focus{
    .sider {
      display: block;
    }
  };
  &:hover {
    cursor: pointer;
  };
}
